.postImg {
    width: 100%;
    object-fit: contain;
}

.postText {
    font-weight: normal;
    padding: 20px;
}

.avatar {
    width: 30px;
    height: 30px;
    border-radius: 100%;
    border: 3px solid #DC316F;
    margin-right: 12px;
    cursor: pointer;
}

.header {
    display: flex;
    align-items: center;
    padding: 12px;
    justify-content: space-between;
}

.nickname {
    font-weight: 600;
    cursor: pointer;
    color: #262626;
    font-size: 14px;
}

.post {
    max-width: 614px;
    display: flex;
    justify-content: center;
    align-content: center;
    margin: 25px auto;
    border: 1px solid lightgray;
}

.name {
    display: flex;
    align-items: center;
}

.circles {
    font-size: 20px;
    font-weight: bold;
    cursor: pointer;
}